import ArrowOutwardIcon from '@mui/icons-material/ArrowOutward';
import './App.css';
import MenuBar from './components/MenuBar';
import myphoto from './MAGGIE_HS_IU-1.jpg';
import project3 from './egghunter.jpg';
import { useState, useEffect } from 'react';
import NavMenu from './components/NavMenu';

function App() {
  const [mobileNavOpen, setMobileNavOpen] = useState(false);
  const [showMobileNavMenu, setShowMobileNavMenu] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 767) {
      setShowMobileNavMenu(false);
    } else setShowMobileNavMenu(true);
  }, []);

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth > 767) {
        setShowMobileNavMenu(false);
        setMobileNavOpen(false);
      } else setShowMobileNavMenu(true);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <div className="App">
      <header>{showMobileNavMenu ? null : <MenuBar />}</header>
      <body className="Body">
        <section id="home">
          <h1> Hello World </h1>
          <div className="subtitle">
            <ArrowOutwardIcon fontSize="inherit" className="icon" />
            <div>
              <h2>Maggie Chew</h2>
              <h3>Full Stack Web Development</h3>
            </div>
          </div>
        </section>

        <section id="about">
          <h2>About Me</h2>
          <div className="about">
            <img src={myphoto} alt="Maggie Chew Headshot" />
            <div className="about-text">
              <span>
                <b>Hi, my name is Maggie, and I'm a Full-Stack Developer.</b>
              </span>
              <p>
                I love to solve complex problems, to constantly learn and
                challenge myself, and to make great things happen with great
                people.
              </p>
              <p>
                Hailing originally from Calgary, I first moved to Toronto, then
                Boston, to pursue Bachelor's and Master's Degrees in Music
                Performance. After sustaining an injury to my wrist, I then
                pivoted to arts management, and then the non-profit world at
                large, where I worked for the last decade. In 2022, I decided it
                was time to make a change, and took a chance on a program at
                Inception U, becoming a Full-Stack Developer.
              </p>
              <p>
                I'm so thrilled to be in my dream career, and can't wait to join
                your team!
              </p>
            </div>
          </div>
        </section>
        <section id="projects">
          <div>
            <h2>Projects</h2>
            <div className="project-table">
              <p> More information to come soon!</p>
              <p>
                More information on all my projects can be found on my Linkedin
              </p>
              <img src={project3} alt="EggHunter" />
              <h4>Inception U Project 3: EggHunter</h4>
              <a
                className="App-link"
                href="http://www.egghunter.site"
                target="_blank"
                rel="noopener noreferrer"
              >
                <p>www.egghunter.site</p>
              </a>
            </div>
          </div>
        </section>
        <section id="contact">
          <h2>Get In Touch</h2>

          <div className="links">
            <a
              className="App-link"
              href="https://www.linkedin.com/in/maggie-chew-00226273"
              target="_blank"
              rel="noopener noreferrer"
            >
              Find me on Linkedin{' '}
            </a>
            <br />
            <a
              className="App-link"
              href="https://github.com/maggiechew"
              target="_blank"
              rel="noopener noreferrer"
            >
              Find me on Github{' '}
            </a>
            <br />
            <a
              className="App-link"
              href="https://docs.google.com/document/d/e/2PACX-1vRYZtF8Nx3NinboP4cXY7MCW9jf1Lic7eze1Y1AD-SUJ6rG6p46k5Fp90j7ngH6fQ/pub"
              target="_blank"
              rel="noopener noreferrer"
            >
              Read my Resume{' '}
            </a>
          </div>
        </section>
      </body>
    </div>
  );
}

export default App;
