import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Toolbar from '@mui/material/Toolbar';
import * as React from 'react';

export default function MenuBar() {
  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar
        position="fixed"
        sx={{
          backgroundColor: '#ff00d5',
          display: 'flex',
          justifyContent: 'right',
          boxShadow: 0,
        }}
      >
        <Toolbar sx={{ justifyContent: 'end' }}>
          <Button color="inherit" size='large' href="#home">
            Home
          </Button>
          <Button color="inherit" size='large' href="#about">
            About
          </Button>
          <Button color="inherit" size='large'  href="#projects">
            Projects
          </Button>
          <Button color="inherit" size='large'  href="#contact">
            Contact
          </Button>
          <Button
            color="inherit"
            variant="outlined"
            href="https://docs.google.com/document/d/e/2PACX-1vRYZtF8Nx3NinboP4cXY7MCW9jf1Lic7eze1Y1AD-SUJ6rG6p46k5Fp90j7ngH6fQ/pub"
          >
            Resume
          </Button>
        </Toolbar>
      </AppBar>
    </Box>
  );
}
